let canvas = null, context = null;
function getCanvas() {
    // get a reference to the canvas element
    canvas = document.getElementById("canvas");
    // create a canvas context to draw stuff with
    context = canvas.getContext("2d");
}

const BLOCK_SIZE = 20;

function randomNumber(min, max) {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1)) + min;
}

function clearCanvas() {
    // Store the current transformation matrix
    context.save();

    // Use the identity matrix while clearing the canvas
    context.setTransform(1, 0, 0, 1, 0, 0);
    context.clearRect(0, 0, canvas.width, canvas.height);

    // Restore the transform
    context.restore();
}

function hexToRgb(hex) {
    // Expand shorthand form (e.g. "03F") to full form (e.g. "0033FF")
    var shorthandRegex = /^#?([a-f\d])([a-f\d])([a-f\d])$/i;
    hex = hex.replace(shorthandRegex, function(m, r, g, b) {
        return r + r + g + g + b + b;
    });

    var result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
    return result ? {
        r: parseInt(result[1], 16),
        g: parseInt(result[2], 16),
        b: parseInt(result[3], 16)
    } : null;
}

function drawRect(width, height, x, y, fill = '', stroke = '', strokeWidth = 0, alpha = 1) {
    context.beginPath()
    context.rect(width, height, x, y);
    if (fill) {
        context.fillStyle = fill;
        if (alpha < 1) {
            const rgb = hexToRgb(context.fillStyle)
            context.strokeStyle = `rgba(${rgb.r}, ${rgb.g}, ${rgb.b}, ${alpha})`
        }
        context.fill();
    }
    if (stroke) {
        context.lineWidth = strokeWidth
        context.strokeStyle = stroke
        if (alpha < 1) {
            const rgb = hexToRgb(context.fillStyle)
            context.strokeStyle = `rgba(${rgb.r}, ${rgb.g}, ${rgb.b}, ${alpha})`
        }
        context.stroke()
    }
}

function drawCircle(x, y, radius, fill = '', stroke = '', strokeWidth = 0, alpha = 1) {
    context.beginPath()
    context.arc(x, y, radius, 0, 2 * Math.PI, false)
    if (fill) {
        context.fillStyle = fill
        if (alpha < 1) {
            const rgb = hexToRgb(context.fillStyle)
            context.strokeStyle = `rgba(${rgb.r}, ${rgb.g}, ${rgb.b}, ${alpha})`
        }
        context.fill()
    }
    if (stroke) {
        context.lineWidth = strokeWidth
        context.strokeStyle = stroke
        if (alpha < 1) {
            const rgb = hexToRgb(context.strokeStyle)
            context.strokeStyle = `rgba(${rgb.r}, ${rgb.g}, ${rgb.b}, ${alpha})`
        }
        context.stroke()
    }
}

function drawText(text, size, x, y) {
    context.beginPath()
    context.font = size + 'px verdana';
    context.fillStyle = 'black';
    context.fillText(text, x, y);
}

function calculateDistance(p1, p2) {
    var a = p2.x - p1.x;
    var b = p2.y - p1.y;

    return Math.sqrt(a * a + b * b);
}

// Basic collision detection for rectangle intersection (NOTE: again, this would
// live inside the system itself)
function doesIntersect(obj1, obj2) {
    // Takes in two objects with position and size properties
    //  obj1: player controlled position and size
    //  obj2: object to check
    //
    var rect1 = {
        x: obj1.position.x - obj1.size,
        y: obj1.position.y - obj1.size,
        height: obj1.size * 2,
        width: obj1.size * 2
    };
    var rect2 = {
        x: obj2.position.x - obj2.size,
        y: obj2.position.y - obj2.size,
        height: obj2.size * 2,
        width: obj2.size * 2
    };

    return (rect1.x < rect2.x + rect2.width &&
        rect1.x + rect1.width > rect2.x &&
        rect1.y < rect2.y + rect2.height &&
        rect1.height + rect1.y > rect2.y);
}

/******************/
/***** ENTITY *****/
/******************/
// For tutorial purposes, we'll set up a global object containing 
// references to all objects.
const ECS = {
    Components: {},
    systems: {},
    entities: {},
    Game: {},
};
ECS.Entity = function Entity() {
    // Generate a pseudo random ID
    this.id = (+new Date()).toString(16) +
        (Math.random() * 100000000 | 0).toString(16) +
        ECS.Entity.prototype._count;

    // increment counter
    ECS.Entity.prototype._count++;

    // The component data will live in this object
    this.components = {};

    return this;
};
// keep track of entities created
ECS.Entity.prototype._count = 0;
ECS.Entity.prototype.addComponent = function addComponent(component) {
    // Add component data to the entity
    // NOTE: The component must have a name property (which is defined as 
    // a prototype protoype of a component function)
    this.components[component.name] = component;
    return this;
};
ECS.Entity.prototype.removeComponent = function removeComponent(componentName) {
    // Remove component data by removing the reference to it.
    // Allows either a component function or a string of a component name to be
    // passed in
    var name = componentName; // assume a string was passed in

    if (typeof componentName === 'function') {
        // get the name from the prototype of the passed component function
        name = componentName.prototype.name;
    }

    // Remove component data by removing the reference to it
    delete this.components[name];
    return this;
};
ECS.Entity.prototype.print = function print() {
    // Function to print / log information about the entity
    console.log(JSON.stringify(this, null, 4));
    return this;
};

/**********************/
/***** COMPONENTS *****/
/**********************/
// Health
// --------------------------------------
ECS.Components.Health = function ComponentHealth(value) {
    value = value || 20;
    this.value = value;
    return this;
};
ECS.Components.Health.prototype.name = 'health';
// Player
// --------------------------------------
ECS.Components.Player = function ComponentPlayer() {
    this.playerControllable = true;
    this.score = 0;
    return this;
};
ECS.Components.Player.prototype.name = 'player';
// Enemy
// --------------------------------------
ECS.Components.Enemy = function ComponentEnemy(params) {
    params = params || {};
    this.damage = params.damage;
    return this;
};
ECS.Components.Enemy.prototype.name = 'enemy';
// Projectile
// --------------------------------------
ECS.Components.Projectile = function ComponentProjectile(damage) {
    this.damage = damage;
    return this;
};
ECS.Components.Projectile.prototype.name = 'projectile';
// Particle
// --------------------------------------
ECS.Components.Particle = function ComponentParticle(decay) {
    this.decay = decay;
    this.createdTimestamp = Date.now();
    return this;
};
ECS.Components.Particle.prototype.name = 'particle';
// Velocity
// --------------------------------------
ECS.Components.Velocity = function ComponentVelocity(params) {
    params = params || {};
    this.x = params.x;
    this.y = params.y;
    return this;
};
ECS.Components.Velocity.prototype.name = 'velocity';
// Target
// --------------------------------------
ECS.Components.Target = function ComponentTarget(params) {
    params = params || {};
    this.id = params.id;
    this.damage = params.damage;
    return this;
};
ECS.Components.Target.prototype.name = 'target';
// Shooting
// --------------------------------------
ECS.Components.Shooting = function ComponentShooting(params) {
    params = params || {};
    this.damage = params.damage;
    this.cooldown = params.cooldown;
    this.lastTimestamp = 0;
    return this;
};
ECS.Components.Shooting.prototype.name = 'shooting';
// Position
// --------------------------------------
ECS.Components.Position = function ComponentPosition(params) {
    params = params || {};
    this.x = params.x;
    this.y = params.y;
    return this;
};
ECS.Components.Position.prototype.name = 'position';
// Appearance 
// --------------------------------------
ECS.Components.Appearance = function ComponentAppearance(params) {
    params = params || {};
    this.shape = params.shape || 'square';
    this.size = params.size || BLOCK_SIZE;
    this.fill = params.fill;
    this.stroke = params.stroke;
    this.strokeWidth = params.strokeWidth;
    this.alpha = params.alpha || 1;
    return this;
};
ECS.Components.Appearance.prototype.name = 'appearance';


/*******************/
/***** SYSTEMS *****/
/*******************/
// Render
// --------------------------------------
ECS.systems.render = function systemRender(entities) {
    // This happens each tick, so we need to clear out the previous rendered
    // state
    clearCanvas();

    for (const entityId in entities) {
        const curEntity = entities[entityId];

        if (curEntity.components.appearance && curEntity.components.health) {
            drawText(
                curEntity.components.health.value,
                10,
                curEntity.components.position.x + 1,
                curEntity.components.position.y - 4
            );
        }

        if (curEntity.components.appearance.shape == 'square') {
            drawRect(
                curEntity.components.position.x,
                curEntity.components.position.y,
                curEntity.components.appearance.size,
                curEntity.components.appearance.size,
                curEntity.components.appearance.fill,
                curEntity.components.appearance.stroke,
                curEntity.components.appearance.strokeWidth,
                curEntity.components.appearance.alpha,
            )
        }

        if (curEntity.components.appearance.shape == 'circle') {
            drawCircle(
                curEntity.components.position.x,
                curEntity.components.position.y,
                curEntity.components.appearance.size,
                curEntity.components.appearance.fill,
                curEntity.components.appearance.stroke,
                curEntity.components.appearance.strokeWidth,
                curEntity.components.appearance.alpha,
            )
        }
    }

    // SCORE
    drawText(
        'SCORE: ' + ECS.Game.player.components.player.score,
        18, canvas.width - 135, 20
    );

    if (ECS.Game.isGameOver) {
        drawText(
            'GAME OVER',
            32, canvas.width / 2 - 100, canvas.height / 2 - 30
        );
    }
};
// Physics
// --------------------------------------
ECS.systems.physics = function systemPhysics(entities) {
    for (const entityId in entities) {
        const curEntity = entities[entityId];

        if (curEntity.components.velocity) {
            curEntity.components.position.x += curEntity.components.velocity.x;
            curEntity.components.position.y += curEntity.components.velocity.y;
        }

        if (curEntity.components.position.x < 0 ||
            curEntity.components.position.x > canvas.width ||
            curEntity.components.position.y < 0 ||
            curEntity.components.position.y > canvas.height) {
            delete entities[entityId];
        }
    }
};
// Collision
// --------------------------------------
function createDeathParticles(position){
    for (let i=0; i<randomNumber(6,9); i++) {
        const particle = new ECS.Entity();
        particle.addComponent(new ECS.Components.Particle(700));
        particle.addComponent(new ECS.Components.Position({x: position.x, y: position.y}));

        const length_x = position.x * randomNumber(-1,1) * (randomNumber(1,9)/10);
        const length_y = position.y * randomNumber(-1,1) * (randomNumber(1,9)/10);
        const length = Math.sqrt(
            length_x * length_x +
            length_y * length_y
        );
        particle.addComponent(new ECS.Components.Velocity({
            x: (length_x / length) * 0.4,
            y: (length_y / length) * 0.4,
        }));

        particle.addComponent(new ECS.Components.Appearance({
            shape: 'circle',
            size: 4,
            stroke: 'grey',
            strokeWidth: 2,
        }));
        ECS.entities[particle.id] = particle;
    }
}
ECS.systems.collision = function systemCollision(player, entities) {
    const enemies = Object.values(entities).filter((e) => { if (e.components.enemy) return e; });
    const projectiles = Object.values(entities).filter((e) => { if (e.components.projectile) return e; });
    for (const enemy of enemies) {

        for (const projectile of projectiles) {
            if (doesIntersect(
                {
                    position: enemy.components.position,
                    size: enemy.components.appearance.size
                },
                {
                    position: projectile.components.position,
                    size: projectile.components.appearance.size
                })) {
                enemy.components.health.value -= projectile.components.projectile.damage;
                delete entities[projectile.id];
                if (enemy.components.health.value <= 0) {
                    player.components.player.score += 10;
                    createDeathParticles(enemy.components.position);
                    delete entities[enemy.id];
                }
            }
        }

        if (doesIntersect(
            {
                position: enemy.components.position,
                size: enemy.components.appearance.size
            },
            {
                position: player.components.position,
                size: player.components.appearance.size
            })) {
            player.components.health.value -= enemy.components.enemy.damage;
            delete entities[enemy.id];
            if (player.components.health.value <= 0) {
                player.components.health.value = 0
                ECS.Game.isGameOver = true;
            }
        }
    }
};
// Decay
// --------------------------------------
ECS.systems.decay = function systemDecay(entities) {
    let nowInMilliseconds = Date.now();
    for (const entityId in entities) {
        const curEntity = entities[entityId];

        if (curEntity.components.particle) {
            if (curEntity.components.particle.decay + curEntity.components.particle.createdTimestamp < nowInMilliseconds) {
                delete entities[entityId];
            } else {
                const pastTime = nowInMilliseconds - curEntity.components.particle.createdTimestamp;
                const alpha = (curEntity.components.particle.decay - pastTime) / curEntity.components.particle.decay;
                curEntity.components.appearance.alpha = alpha;
            }
        }
    }
}
// Shooting
// --------------------------------------
ECS.systems.shooting = function systemShooting(player, entities) {
    let nowInMilliseconds = Date.now();
    if (player.components.shooting.lastTimestamp + player.components.shooting.cooldown < nowInMilliseconds) {
        const enemies = Object.values(entities).filter((e) => { if (e.components.enemy) return e; });
        // const targetEnemy = enemies[0]; //TODO
        const targetEnemy = enemies.map((enemy) => {
            return {
                ...enemy,
                distance: calculateDistance(
                    {
                        x: player.components.position.x,
                        y: player.components.position.y
                    }, {
                        x: enemy.components.position.x,
                        y: enemy.components.position.y
                    }
                )
            }
        }).sort(
            (e1, e2) => e1.distance - e2.distance
        ).find((enemy) => {
            const targetsOnEnemy = Object.values(entities).filter((e) => e.components.target && enemy.id == e.components.target.id);
            const totalDamage = targetsOnEnemy.reduce((parcialDamage, e) => parcialDamage + e.components.target.damage, 0);
            return totalDamage < enemy.components.health.value;
        });

        const projectile = new ECS.Entity();
        projectile.addComponent(new ECS.Components.Projectile(player.components.shooting.damage));
        projectile.addComponent(new ECS.Components.Target({
            id: targetEnemy.id,
            damage: player.components.shooting.damage,
        }));
        projectile.addComponent(new ECS.Components.Position({
            x: player.components.position.x + BLOCK_SIZE / 2,
            y: player.components.position.y + BLOCK_SIZE / 2,
        }));
        projectile.addComponent(new ECS.Components.Appearance({
            shape: 'circle',
            size: 3,
            fill: 'yellow',
            stroke: 'red',
            strokeWidth: 1,
        }));

        const length_x = targetEnemy.components.position.x - projectile.components.position.x;
        const length_y = targetEnemy.components.position.y - projectile.components.position.y;
        const length = Math.sqrt(
            length_x * length_x +
            length_y * length_y
        );
        projectile.addComponent(new ECS.Components.Velocity({
            x: (length_x / length) * 2,
            y: (length_y / length) * 2,
        }));

        ECS.entities[projectile.id] = projectile;

        player.components.shooting.lastTimestamp = nowInMilliseconds;
    }
};
// Populate
// --------------------------------------
ECS.systems.populate = function systemPopulate(player) {
    const enemies = Object.values(ECS.entities).filter((e) => { if (e.components.enemy) return e; });
    if (enemies.length < ECS.Game.maxEnemies) {
        const isBoss = randomNumber(1, 10) == 10;
        const enemy = new ECS.Entity();
        enemy.addComponent(new ECS.Components.Health(isBoss ? 15 : 5));
        enemy.addComponent(new ECS.Components.Enemy({ damage: isBoss ? 5 : 1 }));

        const sides = ['TOP', 'LEFT', 'BOTTOM', 'RIGHT'];
        let enemyPosition;
        switch (sides[randomNumber(0, 3)]) {
            case 'TOP':
                enemyPosition = new ECS.Components.Position({
                    x: 0,
                    y: randomNumber(0, canvas.height),
                })
                break;
            case 'LEFT':
                enemyPosition = new ECS.Components.Position({
                    x: randomNumber(0, canvas.width),
                    y: 0,
                })
                break;
            case 'BOTTOM':
                enemyPosition = new ECS.Components.Position({
                    x: canvas.width,
                    y: randomNumber(0, canvas.height),
                })
                break;
            case 'RIGHT':
                enemyPosition = new ECS.Components.Position({
                    x: randomNumber(0, canvas.width),
                    y: canvas.height,
                })
                break;
            default:
            // code block
        }
        enemy.addComponent(enemyPosition);

        const baseSpeed = 1.2;
        const length_x = player.components.position.x - enemy.components.position.x;
        const length_y = player.components.position.y - enemy.components.position.y;
        const length = Math.sqrt(
            length_x * length_x +
            length_y * length_y
        );
        enemy.addComponent(new ECS.Components.Velocity({
            x: (length_x / length) * baseSpeed,
            y: (length_y / length) * baseSpeed,
        }));

        enemy.addComponent(new ECS.Components.Appearance({
            shape: 'square',
            size: isBoss ? BLOCK_SIZE + 10 : BLOCK_SIZE - 5,
            fill: isBoss ? 'deepskyblue' : 'skyblue',
            stroke: 'black',
            strokeWidth: 2,
        }));

        ECS.entities[enemy.id] = enemy;
    }
};


/*********************/
/***** MAIN GAME *****/
/*********************/
function createPlayer() {
    const player = new ECS.Entity();
    player.addComponent(new ECS.Components.Player());
    player.addComponent(new ECS.Components.Health(20));
    player.addComponent(new ECS.Components.Shooting({
        damage: 5,
        cooldown: 325,
    }));
    player.addComponent(new ECS.Components.Position({
        x: canvas.width / 2 - BLOCK_SIZE / 2,
        y: canvas.height / 2 - BLOCK_SIZE / 2,
    }));
    player.addComponent(new ECS.Components.Appearance({
        shape: 'square',
        size: BLOCK_SIZE,
        fill: 'red',
        stroke: 'black',
        strokeWidth: 2,
    }));
    ECS.entities[player.id] = player;
    return player;
}

ECS.Game = {
    isStarted: false,
    isPause: false,
    isGameOver: false,
    player: {},
    interval: null,
    maxEnemies: 10,
    gameLoop: function () {
        ECS.systems.populate(this.player);
        ECS.systems.shooting(this.player, ECS.entities);
        ECS.systems.collision(this.player, ECS.entities);
        ECS.systems.decay(ECS.entities);
        ECS.systems.physics(ECS.entities);
        ECS.systems.render(ECS.entities);
    },
    start: function () {
        getCanvas();
        clearInterval(this.interval);
        this.interval = setInterval(function () {
            if (ECS.Game.isStarted && !ECS.Game.isPause && !ECS.Game.isGameOver) ECS.Game.gameLoop();
        }, 20)

        this.isStarted = true;
        this.player = createPlayer();
    },
    pause: function () {
        if (!ECS.Game.isStarted) return;
        this.isPause = !this.isPause;
    },
    reset: function () {
        this.isStarted = false;
        this.isPause = false;
        this.isGameOver = false;

        clearCanvas();
        ECS.entities = {};
    },
}

window.ECS = ECS;
export default ECS